<template>
    <div>
        <div class="person">
            <button class="add addPerson" @click="addPerson">新增</button>
            <div class="info" v-show="isAdd">
                <input class="newInput" type="text" placeholder="姓名" v-model="newPerson.name">
                <input class="newInput" type="text" placeholder="年龄" v-model="newPerson.age">
                <input class="newInput" type="text" placeholder="性别" v-model="newPerson.sex">
                <button class="add nameSure" @click="nameSure">确认</button>
            </div>
        </div>
        <table>
            <tr>
                <th> 姓名 </th>
                <th> 年龄 </th>
                <th> 性别 </th>
                <th> 操作 </th>
            </tr>
            <tr v-for="(person,index) in people">
                <td>{{person.name}}</td>
                <td>{{person.age}}</td>
                <td>{{person.sex}}</td>
                <td>
                    <button @click="edit(index)">编辑</button>
                    <button @click="deletePerson(index)">删除</button>
                </td>
            </tr>
        </table>
        <div class="editPerson info" v-show="isEdit">
            <input class="newInput" type="text" placeholder="姓名" v-model="newPerson.name">
            <input class="newInput" type="text" placeholder="年龄" v-model="newPerson.age">
            <input class="newInput" type="text" placeholder="性别" v-model="newPerson.sex">
            <button class="add nameSure" @click="editSure">确认</button>
            <button class="add nameSure" @click="exitEdit">取消</button>
        </div>
        <Footer></Footer>
    </div>
</template>

<script>
    import Footer from '../Footer'
    export default {
        name: "Person",
        data() {
            return {
                people: [
                    {name: 'wang', age: 12, sex: 'man'},
                    {name: 'gang', age: 23, sex: 'woman'},
                    {name: 'hello', age: 15, sex: 'man'}
                ],
                isAdd:false,
                newName:'',
                newPerson:{
                    name:'',age:'',sex:'man'
                },
                isEdit:false,
                editId:0
            }
        },
        components: {
            Footer
        },
        methods:{
            addPerson() {
                this.isAdd=true;
            },
            nameSure() {
                if(this.newPerson.name==''||this.newPerson.age==''||this.newPerson.sex==''){
                    return false;
                }else{
                    this.people.push(this.newPerson);
                    this.newPerson={
                        name:'',age:'',sex:'man'
                    }
                };
                this.isAdd=false;
            },
            deletePerson(index) {
                this.people.splice(index,1)
            },
            edit(index) {
                this.isEdit=true;
                this.editId=index;
                this.newPerson={
                    name:this.people[index].name,
                    age:this.people[index].age,
                    sex:this.people[index].sex,
                }
            },
            editSure() {
                this.people[this.editId]=this.newPerson;
                this.isEdit=false;
            },
            exitEdit() {
                this.isEdit=false
            }
        }
    }
</script>

<style scoped>

</style>


