import Vue from 'vue'
import Router from 'vue-router'
import Main from '../components/pages/Main'
import Person from '../components/pages/Person'

Vue.use(Router)

export default new Router({
    routes: [
        /* {
           path: '/',
           name: 'HelloWorld',
           component: HelloWorld
         }*/
        {
            path:'/',
            name:'Main',
            component:Main
        },
        {
            path:'/Person',
            name:'Person',
            component:Person
        }
    ]
})



